.slidingVertical{display:inline;line-height:1.25;text-indent:8px}.slidingVertical span{-webkit-animation:topToBottom 10s linear infinite 0s;animation:topToBottom 10s linear infinite 0s;opacity:0;overflow:hidden;position:absolute}.slidingVertical span:nth-child(2){-webkit-animation-delay:2.5s;animation-delay:2.5s}.slidingVertical span:nth-child(3){-webkit-animation-delay:5s;animation-delay:5s}.slidingVertical span:nth-child(4){-webkit-animation-delay:7.5s;animation-delay:7.5s}@-webkit-keyframes topToBottom{0%{opacity:0}6%{-webkit-transform:translateY(-30px);opacity:0}10%{-webkit-transform:translateY(0);opacity:1}25%{-webkit-transform:translateY(0);opacity:1}29%{-webkit-transform:translateY(30px);opacity:0}80%{opacity:0}to{opacity:0}}